import {playMode} from "common/js/config";

const state={
  //歌手信息
  singer:{},
  //歌曲是否播放
  playing:false,
  //是否全屏
  fullScreen:false,
  //当前播放歌曲列表(顺序，随机)
  playlist:[],
  //传过来的初始播放列表
  sequenceList:[],
  //播放模式
  mode:playMode.sequence,
  //当前播放的索引
  currentIndex:-1
}
export default state



