<template>
    <div class="player" v-show="playlist.length>0">
      <transition name="normal"
                  @enter="enter"
                  @after-enter="afterEnter"
                  @leave="leave"
                  @after-leave="afterLeave"
      >
        <div class="normal-player" v-show="fullScreen">
          <div class="background">
            <img width="100%" height="100%" :src="currentSong.image" alt="">
          </div>
          <div class="top">
            <div class="back" @click="back">
              <i class="icon-back"></i>
            </div>
            <div class="title" v-html="currentSong.name"></div>
            <h2 class="subtitle" v-html="currentSong.singer"></h2>
          </div>
          <!--中部DOM-->
          <div class="middle">
            <div class="middle-l">
              <div class="cd-wrapper" ref="cdWrapper">
                <div class="cd" :class="cdCls">
                  <img class="image" :src="currentSong.image">
                </div>
              </div>
            </div>
          </div>
          <!--底部DOM-->
          <div class="bottom">
            <!--歌曲播放进度条-->
            <div class="progress-wrapper">
              <span class="time time-l">{{format(currentTime)}}</span>
              <div class="progress-bar-wrapper">
                <!--进度条基础组件-->
                <progress-bar @percentChange="onProgressBarChange" :percent="percent()"></progress-bar>
              </div>
              <span class="time time-r">{{format(currentSong.duration)}}</span>
            </div>
            <div class="operators">
              <!--改变循环模式-->
              <div class="icon i-left" @click="changeMode">
                <i :class="iconMode"></i>
              </div>
              <div class="icon i-left">
                <i class="icon-prev" @click="prev"></i>
              </div>
              <div class="icon i-center">
                <i @click="togglePlaying" :class="playIcon"></i>
              </div>
              <div class="icon i-right">
                <i class="icon-next" @click="next"></i>
              </div>
              <div class="icon i-right">
                <i class="icon icon-not-favorite"></i>
              </div>
            </div>
          </div>
        </div>
      </transition>
      <transition name="mini">
        <div class="mini-player" @click="open" v-show="!fullScreen">
          <div class="icon">
            <img :class="cdCls" width="40" height="40" alt="" :src="currentSong.image">
          </div>
          <div class="text">
            <h2 class="name" v-html="currentSong.name"></h2>
            <p class="desc" v-html="currentSong.singer"></p>
          </div>
          <div class="control">
            <!--mini播放器的进度圆圈-->
            <progress-circle :radius=radius :percent="percent()">
              <i class="icon-mini" @click.stop="togglePlaying" :class="miniIcon"></i>
            </progress-circle>
          </div>
          <div class="control">
            <i class="icon-playlist"></i>
          </div>
        </div>
      </transition>
      <audio ref="audio"
             :src="currentSong.url"
             @canplay="ready"
             @error="error"
             @timeupdate="updateTime"
             @ended="end"
      ></audio>
    </div>
</template>

<script>
    import animations from 'create-keyframe-animation'
    import ProgressCircle from 'base/progress-circle/progress-circle'
    import {mapGetters,mapMutations} from 'vuex'
    import {prefixStyle} from "common/js/dom";
    import ProgressBar from "base/progress-bar/progress-bar"
    import {playMode} from "common/js/config";
    import {shuffle} from "common/js/util";

    const transform = prefixStyle('transform')



    export default {
        name: "player",
        data(){
          return {
            songReady:false,
            currentTime:0,
            radius:32
          }
        },
        computed:{
          playIcon(){
            return this.playing ? 'icon-pause':'icon-play'
          },
          miniIcon(){
            return this.playing ? 'icon-pause-mini' : "icon-play-mini"
          },
          cdCls(){
            return this.playing ? 'play' : 'play pause'
          },
          ...mapGetters([
            'fullScreen',
            'playlist',
            "currentSong",
            "playing",
            "currentIndex",
            "mode",
            "sequenceList"
          ]),
          iconMode(){
            return this.mode=== playMode.sequence? 'icon-sequence' : this.mode===playMode.loop?'icon-loop' : 'icon-random'
          }
        },
        methods:{
          back(){
            // 这种修改state值是典型的错误的，必须通过mutations来修改state
            // this.fullScreen = false
            this.setFullScreen(false)
          },
          open(){
            this.setFullScreen(true)
          },
          enter(el,done){
            const {x,y,scale} = this._getPosAndScale()
            let animation = {
              0:{
                transform:`translate3d(${x}px,${y}px,0) scale(${scale})`
              },
              60:{
                transform:`translate3d(0,0,0) scale(1.1)`
              },
              100:{
                transform:`translate3d(0,0,0) scale(1)`
              }
            }
            animations.registerAnimation({
              name:'move',
              animation,
              presets:{
                duration:400,
                esaing:'linear'
              }
            })
            animations.runAnimation(this.$refs.cdWrapper,'move',done)
          },
          afterEnter(el){
            animations.unregisterAnimation('move')
            this.$refs.cdWrapper.style.animation = ''
            this.$refs.cdWrapper.style[transform] = ''
          },
          leave(el,done){
            this.$refs.cdWrapper.style.transition = 'all 0.4s'
            const {x,y,scale} = this._getPosAndScale()
            this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
            this.$refs.cdWrapper.addEventListener('transitionend',done)
          },
          afterLeave(el){
            animations.unregisterAnimation('move')
            this.$refs.cdWrapper.style.animation = ''
            this.$refs.cdWrapper.style[transform] = ''
          },
          //控制是否开关
          togglePlaying(){
            this.setPlayingState(!this.playing)
          },
          end(){
            if(this.mode===playMode.loop){
              this.loop();
            }else{
              this.next()
            }
          },
          loop(){
            this.$refs.audio.currentTime = 0
            this.$refs.audio.play()
          },
          next(){
            if(!this.songReady){
              return
            }
            let index = this.currentIndex + 1
            if(index===this.playlist.length){
              index=0
            }
            this.setCurrentIndex(index)
            if(!this.playing){
              this.togglePlaying()
            }
            this.songReady = false
          },
          prev(){
            if(!this.songReady){
              return
            }
            let index = this.currentIndex -1
            if(index===-1){
              index = this.playlist.length-1
            }
            this.setCurrentIndex(index)
            if(!this.playing){
              this.togglePlaying()
            }
            this.songReady = false
          },
          ready(){
            this.songReady = true
          },
          //这里当歌曲发生错误时不能播放了一样将songeady变成true，不然按钮就不可点击了
          error(){

            // console.log("出错了");
            this.songReady = true
          },
          //播放位置更改时触发这个条件
          updateTime(e){
            //当前播放位置已更改时触发，e.target为当前audio标签
            this.currentTime = e.target.currentTime
          },
          //格式化播放时间格式
          format(interval){
            //相当于是Math.floor()
            interval = interval | 0
            const minute = interval/60 | 0
            let second = this._pad(interval%60,2)
            return `${minute}:${second}`
          },
          //由子级发送过来的事件，监听其百分比的变化
          onProgressBarChange(percent){
            //audio标签元素的currentTime可以读也可以设置
            this.$refs.audio.currentTime = this.currentSong.duration * percent
            if(!this.playing){
              this.togglePlaying()
            }
          },
          changeMode(){
            const mode = (this.mode + 1)% 3
            this.setPlayMode(mode)
            //改变循环模式后，修改播放列表
            let list = null
            if(mode===playMode.random){
              list = shuffle(this.sequenceList)
            }else{
              list = this.sequenceList
            }
            //当随机列表发生变化的时候，还是播放的当前那首歌
            this.resetCurrentIndex(list)

            this.setPlayList(list)

          },
          resetCurrentIndex(list){
            // findIndex() 方法返回传入一个测试条件（函数）符合条件的数组第一个元素位置。
            let index = list.findIndex((item)=>{
              return item.id === this.currentSong.id
            })
            this.setCurrentIndex(index)
          },
          //补零函数
          _pad(num,n=2){
            //len为秒数的长度
            let len = num.toString().length
            //秒数的长度小于2时
            if(len<n){
             num = '0' + num
           }
            return num
          },
          _getPosAndScale(){
            const targetWidth = 40
            const paddingLeft = 40
            const paddingBottom = 30
            const paddingTop = 80
            const width = window.innerWidth*0.8
            const scale = targetWidth/width
            const x = -(window.innerWidth/2 - paddingLeft)
            const y = window.innerHeight - paddingTop - width/2-paddingBottom
            return {x,y,scale}
          },
          percent(){
            return this.currentTime/this.currentSong.duration
          },
          ...mapMutations({
            setFullScreen:'SET_FULL_SCREEN',
            setPlayingState:'SET_PLAYING_STATE',
            setCurrentIndex:'SET_CURRENT_INDEX',
            setPlayMode:'SET_PLAY_MODE',
            setPlayList:'SET_PLAY_LIST'
          })
        },
        watch:{
          currentSong(newSong,oldSong){
            //解决暂停歌曲时切换模式会自动播放
            if(newSong.id===oldSong.id){
              return
            }
            this.$nextTick(()=>{
              this.$refs.audio.play()
            })
          },
          playing(newPlaying){
            this.$nextTick(()=>{
              const audio = this.$refs.audio
              newPlaying ? audio.play() : audio.pause()
            })

          }
        },
        components:{
          ProgressBar,
          ProgressCircle
        }

    }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: $color-background
      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        opacity: 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-theme
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
      .middle
        position: fixed
        width: 100%
        top: 80px
        bottom: 170px
        white-space: nowrap
        font-size: 0
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 80%
          .cd-wrapper
            position: absolute
            left: 10%
            top: 0
            width: 80%
            height: 100%
            .cd
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 10px solid rgba(255, 255, 255, 0.1)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color:skyblue
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: skyblue
              font-size: $font-size-medium
              &.current
                color: $color-text
      .bottom
        position: absolute
        bottom: 50px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 20px
              border-radius: 5px
              background: $color-text-ll
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 10px 0
          .time
            color: $color-text
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-theme-d
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-highlight-background
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-text
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini
          font-size: 30px
          color: $color-theme-d
        .icon-playlist
          font-size: 30px
          color:$color-theme
        .icon-mini
          font-size: 32px
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>



