//洗牌函数，将数组打乱
export function shuffle(arr) {
  //slice返回一个新的数组
  let _arr = arr.slice()
  for(let i = 0;i < _arr.length;i++){
    let j = getRandomInt(0,i)
    let t = _arr[i]
    _arr[i] = _arr[j]
    _arr[j] = t
  }
  return _arr
}
//辅助随机函数
function getRandomInt(min,max) {
  return parseInt(Math.random() * (max-min+1) + min)
}



